$primary: #dd5555;
$background: #ebebeb;

*, *:before, *:after {
box-sizing: border-box;
}
img { max-width: 100%; height: auto; vertical-align: sub; }

body {
  font-family: 'Montserrat', sans-serif;
  background: $background;
  padding-top: 20px;
}

.btn {
  display: inline-block;
  padding: 6px 14px;
  background: #f2f2f2;
  border-radius: 3px;
  color: #7a7a7a;
  font-size: (13/16) + em;
  transition: background .3s ease, color .3s ease;
  text-decoration: none;
  &:hover {
    background: darken(#f2f2f2, 5%);
    color: darken(#b9bcbc, 15%);
  }
  &.btn-primary {
    background: $primary;
    color: rgba(255,255,255,.9);
    &:hover, &.active {
      background: darken($primary, 5%);
    }
  }
  &.btn-large {
    padding: 12px 28px;
  }
  &.btn-caps {
    text-transform: uppercase;
  }
  &.btn-block {
    display: block;
    width: 100%;
    text-align: center;
  }
}

input[type="text"] {
  width: 100%;
  border: 1px solid #cfcfcf;
  background: $background;
  height: 28px;
  font-size: (12/16) + em;
  padding: 5px;
  outline: none;
  border-radius: 3px;
  margin-bottom: 7px;
  transition: background .3s ease;
}
input[type="text"]:focus {
  background: lighten($background, 2%);
}

.checkbox {
  color: #ccc;
  text-shadow: 1px 1px 1px rgba(255,255,255,.4);
  font-size: (11/16) + em;
  margin-bottom: 7px;
  display: inline-block;
}

.box {
  background: #f9f9f9;
  box-shadow: 0 0 1px rgba(0,0,0,.2), 0 2px 4px rgba(0,0,0,.1);
  border-radius: 5px;
  margin-bottom: 20px;
  text-shadow: 1px 1px 1px rgba(0,0,0,.1);
  h2 {
    color: $primary;
    font-size: (14/16) + em;
  }
  p {
    font-size: (12/16) + em;
    color: #7d7d7d;
  }
}

.locations {
  padding: 30px;
  text-align: center;
  .avatar {
    border-radius: 50%;
    img {
      border-radius: 50%;
    }
  }
  .details {
    margin-top: 30px;
  }
  .btn {
    margin-top: 30px;
  }
}

.calendar {
  text-align: center;
  .header {
    padding: 10px 0;
  }
  [class*="icon-"]{
    color: $primary;
    position: relative;
    top: 2px;
    margin: 0 5px;
    display: inline-block;
    cursor: pointer;
  }
  .days {
    background: $primary;
    padding: 20px;
    border-radius: 0 0 5px 5px;
    ul {
      margin: 0;
      padding: 0;
    }
    li {
      display: inline-block;
      color: #fff;
      padding: 5px 0;
      width: 30px; height: 30px;
      text-align: center;
      cursor: pointer;
      @media (max-width: 767px) {
        width: 20px; height: 20px;
      }
      &.next, &.previous {
        color: rgba(0,0,0,.2);
      }
    }
  }
}

.weather {
  .header {
    border-radius: 5px 5px 0 0;
    padding: 20px 0;
    background: $primary;
    text-align: center;
    color: #fff;
    font-size: (50/16) + em;
  }
  [class*="icon-"] {
    position: relative;
    top: 2px;
  }
  article {
    text-align: center;
    padding: 5px 0;
  }
}
